import React from "react";
import Layout from "./../components/Layout";
import { Tabs, message } from "antd";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import { hideLoading, showLoding } from "../redux/features/alertSlice";
import { useLocation, useNavigate } from "react-router-dom";

const NotificationPage = () => {
  const location = useLocation();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { user } = useSelector((state) => state.user);

  const handleMarkAllRead = async (id) => {
    try {
      dispatch(showLoding(true));
      const res = await axios.post(
        `${process.env.REACT_APP_API}/api/v1/auth/get-all-notification`,
        { userId: id },
        {
          headers: {
            Authorization: localStorage.getItem("token"),
          },
        }
      );
      dispatch(hideLoading(false));
      if (res.data.success) {
        message.success("mark as read success");

        navigate("/notification");
        // window.location.reload();
      } else {
        message.error("went wrong");
      }
    } catch (error) {
      console.log(error);
    }
  };
  const handleDeleteAllRead = async (id) => {
    try {
      dispatch(showLoding(true));
      const res = await axios.post(
        `${process.env.REACT_APP_API}/api/v1/auth/delete-all-notification`,
        { userId: id },
        {
          headers: {
            Authorization: localStorage.getItem("token"),
          },
        }
      );
      dispatch(hideLoading(false));
      if (res.data.success) {
        message.success(res.data.success);

        navigate("/notification");
        window.location.reload();
      } else {
        message.error("went wrong");
      }
    } catch (error) {
      console.log(error);
    }
  };
  return (
    <Layout>
      <h1 className="m-3 text-center">notification page</h1>

      <Tabs>
        <Tabs.TabPane tab="unRead" key={0}>
          <div className="d-flex justify-content-end">
            <h3 className="p-2" style={{cursor:"pointer"}}  onClick={() => handleMarkAllRead(user._id)}>
              mark all read
            </h3>
          </div>
          {user?.notification.map((n) => (
            <div className="card">
              <div className="card-text">
                <h3>{JSON.stringify(n.message)}</h3>
              </div>
            </div>
          ))}
        </Tabs.TabPane>

        <Tabs.TabPane tab="Read" key={1}>
          <div className="d-flex justify-content-end">
            <h3 className="p-2"  style={{cursor:"pointer"}} onClick={() => handleDeleteAllRead(user._id)}>
              delete all read
            </h3>
          </div>

          {user?.seenNotification.map((n) => (
            <div className="card" style={{ cursor: "pointer" }}>
              <div className="card-text" onClick={()=>navigate(n.data.onClickPath)}>
                {JSON.stringify(n.message)}
              </div>
            </div>
          ))}
        </Tabs.TabPane>
      </Tabs>
    </Layout>
  );
};

export default NotificationPage;
