import React, { useEffect, useState } from "react";
import Layout from "../../components/Layout";
import axios from "axios";
import { message } from "antd";

const Doctors = () => {
  let count = 0;

  const [doctors, setDoctors] = useState([]);

  const getAllDoctors = async () => {
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_API}/api/v1/admin/getAllDoctors`,
        {
          headers: {
            authorization: localStorage.getItem("token"),
          },
        }
      );

      if (res.data.success) {
        // alert("hii");
        // message.success("list is here");
        setDoctors(res.data);
      }
    } catch (error) {
      console.log(error);
    }
  };

  const handleAccountStatus = async (d,status) => {
    try {
      const res = await axios.post(
        `${process.env.REACT_APP_API}/api/v1/admin/changeAcoountStatus`,
        { doctorId: d._id, status },
        {
          headers: {
            authorization: localStorage.getItem("token"),
          },
        }
      );
      if (res?.data?.success) {
        message.success(res.data.message);
        window.location.reload()
      }
    } catch (error) {
      message.error("something went wrong");
    }
  };
  useEffect(() => {
    getAllDoctors();
  }, []);
  return (
    <Layout>
      <table>
        <thead className="head">
          <td>sr. no.</td>
          <td>firstName</td>
          <td>email</td>
          <td>Experience</td>
          <td>specialization</td>
          <td>Actions</td>
        </thead>
        <tbody>
          {doctors?.data?.map((d) => (
            <tr>
              <td>{(count = count + 1)}</td>
              <td>{d.firstName}</td>
              <td>{d.email}</td>
              <td>{d.experince}</td>
              <td>{d.specialization}</td>
              <td>
                {d.status === "pending" ? (
                  <button
                    className="btn btn-success"
                    onClick={() => handleAccountStatus(d,"approved")}
                  >
                    Approve
                  </button>
                ) : (
                  <button className="btn btn-danger">Reject</button>
                )}
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </Layout>
  );
};

export default Doctors;
