import React from "react";
import Layout from "../components/Layout";
import { Col, Collapse, Form, Input, Row, TimePicker, message } from "antd";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { hideLoading, showLoding } from "../redux/features/alertSlice";

import axios from "axios";
const ApplyDoctor = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { user } = useSelector((state) => state.user);
  const handleFinish = async (values) => {
    try {
      // message.success("hii");
      dispatch(showLoding(true));
      const res = await axios.post(
        `${process.env.REACT_APP_API}/api/v1/auth/apply-doctor`,
        { ...values, userId: user._id },
        {
          headers: {
            Authorization: `${localStorage.getItem("token")}`,
          },
        }
      );
      console.log(res);
      dispatch(hideLoading(false));
      if (res?.data?.success) {
        message.success(res.data.message);
        navigate("/");
      } else {
        // console.log();
        message.error("error is here");
      }
    } catch (error) {
      console.log(error);
      dispatch(hideLoading(false));
    }
  };

  return (
    <Layout>
      <h1> hii this is the aplly doctor page</h1>
      <h1 className="text-center">Aplly ApplyDoctor</h1>
      <Form layout="vertical" className="form-d" onFinish={handleFinish}>
        <h4 className="">personal details</h4>
        <Row gutter={20}>
          <Col xs={24} md={24} lg={8}>
            <Form.Item
              label="first name"
              name="firstName"
              required
              rules={[{ required: true }]}
            >
              <Input type="text" placeholder="your name" />
            </Form.Item>
          </Col>{" "}
          <Col xs={24} md={24} lg={8}>
            <Form.Item
              label="last name"
              name="lastName"
              required
              rules={[{ required: true }]}
            >
              <Input type="text" placeholder="your name" />
            </Form.Item>
          </Col>{" "}
          <Col xs={24} md={24} lg={8}>
            <Form.Item
              label="phone"
              name="phone"
              required
              rules={[{ required: true }]}
            >
              <Input type="text" placeholder="your name" />
            </Form.Item>
          </Col>{" "}
          <Col xs={24} md={24} lg={8}>
            <Form.Item
              label="email"
              name="email"
              required
              rules={[{ required: true }]}
            >
              <Input type="text" placeholder="your name" />
            </Form.Item>
          </Col>{" "}
          <Col xs={24} md={24} lg={8}>
            <Form.Item
              label="website"
              name="website"
              required
              rules={[{ required: true }]}
            >
              <Input type="text" placeholder="your name" />
            </Form.Item>
          </Col>{" "}
          <Col xs={24} md={24} lg={8}>
            <Form.Item
              label="address"
              name="address"
              required
              rules={[{ required: true }]}
            >
              <Input type="text" placeholder="your name" />
            </Form.Item>
          </Col>
        </Row>
        <h2>professional details</h2>
        <Row gutter={20}>
          <Col xs={24} md={24} lg={8}>
            <Form.Item
              label="specialization"
              name="specialization"
              required
              rules={[{ required: true }]}
            >
              <Input type="text" placeholder="your name" />
            </Form.Item>
          </Col>{" "}
          <Col xs={24} md={24} lg={8}>
            <Form.Item
              label="experince"
              name="experince"
              required
              rules={[{ required: true }]}
            >
              <Input type="text" placeholder="your name" />
            </Form.Item>
          </Col>{" "}
          <Col xs={24} md={24} lg={8}>
            <Form.Item
              label="feePerConsultation"
              name="feePerConsultation"
              required
              rules={[{ required: true }]}
            >
              <Input type="text" placeholder="your name" />
            </Form.Item>
          </Col>{" "}
          <Col xs={24} md={24} lg={8}>
            <Form.Item label="Timings" name="timings" required>
              <TimePicker.RangePicker format="HH:mm" />
            </Form.Item>
          </Col>
          <Col xs={24} md={24} lg={8}/>
          <Col xs={24} md={24} lg={8} />
        </Row>
        <div className="d-flex justify-content-end">
          <button className="btn btn-primary" type="submit">
            submit
          </button>
        </div>
      </Form>
    </Layout>
  );
};
export default ApplyDoctor;
