import React, { useEffect, useState } from "react";
import Layout from "../components/Layout";
import { DatePicker, TimePicker, message } from "antd";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import { useParams } from "react-router-dom";
import moment from "moment";
import { hideLoading, showLoding } from "../redux/features/alertSlice";

const BookingPage = () => {
  const dispatch = useDispatch();
  const params = useParams();
  const { user } = useSelector((state) => state.user);
  const [doctor, setDoctor] = useState();
  const [date, setDate] = useState();
  const [time, setTime] = useState();
  const [isAvailable, setIsAvailable] = useState();
  const getDoctorData = async () => {
    try {
      const res = await axios.post(
        `${process.env.REACT_APP_API}/api/v1/auth/getSingleDoctor `,
        { doctorId: params.id },
        {
          headers: {
            authorization: localStorage.getItem("token"),
          },
        }
      );
      if (res.data.success) {
        setDoctor(res.data.data);
        message.success(res.data.message);
      } else {
        message.error("smoething went wrong");
      }
    } catch (error) {
      console.log(error);
    }
  };
  const handleAvailability = async () => {
    try {
      dispatch(showLoding(true));
      const res = await axios.post(
        "/api/v1/user/booking-availbility",
        { doctorId: params.doctorId, date, time },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      dispatch(hideLoading(false));
      if (res.data.success) {
        setIsAvailable(true);
        console.log(isAvailable);
        message.success(res.data.message);
      } else {
        message.error(res.data.message);
      }
    } catch (error) {
      dispatch(hideLoading(false));
      console.log(error);
    }
  };
  // =============== booking func
  const handleBooking = async () => {
    try {
      setIsAvailable(true);
      if (!date && !time) {
        return alert("Date & Time Required");
      }
      dispatch(showLoding(true));
      const res = await axios.post(
        `${process.env.REACT_APP_API}/api/v1/auth/book-appointment`,
        {
          doctorId: params.doctorId,
          userId: user._id,
          doctorInfo: doctor,
          userInfo: user,
          date: date,
          time: time,
        },
        {
          headers: {
            Authorization: localStorage.getItem("token"),
          },
        }
      );
      dispatch(hideLoading(false));
      if (res.data.success) {
        message.success(res.data.message);
      }
    } catch (error) {
      dispatch(hideLoading(false));
      console.log(error);
    }
  };

  useEffect(() => {
    getDoctorData();
  }, []);

  return (
    // <Layout>
    //   <h2>Booking </h2>
    //   {/* <h1>{JSON.stringify(doctor)}</h1> */}
    //   <div className="container">
    //     {doctor && (
    //       <div className="">
    //         <h3>
    //           Dr.{doctor.firstName}
    //           {doctor.lastName}
    //         </h3>{" "}
    //         <h3>
    //           Fees{doctor.feesPerConsultation}
    //           {doctor.lastName}
    //         </h3>{" "}
    //         <div className="d-flex flex-column">
    //
    //           <input
    //             type="date"
    //             format="DD-MM-YYYY"
    //        style={{color:"black"}}
    //             // ref={dateInputRef}
    //           />
    //         </div>
    //       </div>
    //     )}
    //   </div>
    // </Layout>

    <Layout>
      <h3>Booking Page</h3>
      <div className="container m-2">
        {doctor && (
          <div>
            <h4>
              Dr.{doctor.firstName} {doctor.lastName}
            </h4>
            <h4>Fees : {doctor.feesPerCunsaltation}</h4>
            <h4>
              Timings : {doctor.timings && doctor.timings[0]} -{" "}
              {doctor.timings && doctor.timings[1]}{" "}
            </h4>
            <div className="d-flex flex-column w-50">
              <DatePicker
                aria-required={"true"}
                className="m-2"
                format="DD-MM-YYYY"
                onChange={(value) => {
                  setDate(moment(value).format("DD-MM-YYYY"));
                }}
              />
              <TimePicker
                aria-required={"true"}
                format="HH:mm"
                className="mt-3"
                onChange={(value) => {
                  setTime(moment(value).format("HH:mm"));
                }}
              />

              <button
                className="btn btn-primary mt-2"
                onClick={handleAvailability}
              >
                Check Availability
              </button>

              <button className="btn btn-dark mt-2" onClick={handleBooking}>
                Book Now
              </button>
            </div>
          </div>
        )}
      </div>
    </Layout>
  );
};

export default BookingPage;
