import React from "react";
import "../styles/LayoutStyles.css";
import { userMenu, adminMenu } from "./../Data/data";
import { Link, Navigate, useLocation, useNavigate } from "react-router-dom";
import { Badge, Form, message } from "antd";
import { useSelector } from "react-redux";
const Layout = ({ children }) => {
  const location = useLocation();
  const navigate = useNavigate();
  const { user } = useSelector((state) => state.user);
  const handleLogout = async () => {
    try {
      localStorage.clear();
      message.success("logout successful");

      navigate("/login");
    } catch (error) {
      console.log(error);
    }
  };
  // ----------doctor menu
  const doctorMenu = [
    { name: "Home", path: "/", icon: "fa-solid fa-house-chimney" },
    {
      name: "Appointment",
      path: "/about",
      icon: "fa-regular fa-calendar-check",
    },

    {
      name: "Profile",
      path: `/doctor/profile/${user?._id}`,
      icon: "fa-thin fa-user",
    },
  ];

  const SidebarMenu = user?.isAdmin
    ? adminMenu
    : user?.isDoctor
    ? doctorMenu
    : userMenu;
  return (
    <div className="">
      <div className="c-layout container-fluid d-flex flex-row ">
        {/* <div className="row"> */}
        <div className=" sideBar ">
          <div className="logo">logo</div>
          <div className="menu">
            {SidebarMenu.map((menu) => {
              const isActive = location.pathname === menu.path;
              return (
                <>
                  <div className={`menu-items ${isActive && "active"}`}>
                    <i className={menu.icon}></i>
                    <Link to={menu.path}>{menu.name}</Link>
                  </div>
                </>
              );
            })}
            <div className={`menu-items `} onClick={handleLogout}>
              <i className="fa-sharp fa-solid fa-right-from-bracket"></i>
              <Link to={"/login"}>logout</Link>
            </div>
          </div>
        </div>
        <div className=" body">
          <div className="header">
            <div className="header-content" style={{ cursor: "pointer" }}>
              <Badge
                count={user?.notification.length}
                onClick={() => {
                  navigate("/notification");
                }}
              >
                <i className="fa-solid fa-bell"></i>
              </Badge>
              <Link className="profile-icon" to={"/profile"}>
                {user?.name}
              </Link>
            </div>
          </div>
          <div className="i-body">{children}</div>
        </div>
      </div>
    </div>
    // </div>
  );
};

export default Layout;
