import React, { useEffect, useState } from "react";
import Layout from "../../components/Layout";
import axios from "axios";
import { message } from "antd";

const Users = () => {
  let count = 0;
  const [users, setUsers] = useState([]);

  const getAllUsers = async () => {
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_API}/api/v1/admin/getAllUsers`,
        {
          headers: {
            authorization: localStorage.getItem("token"),
          },
        }
      );

      if (res.data.success) {
        // alert("hii");
        // message.success("list is here");
        setUsers(res.data);
      }
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    getAllUsers();
  }, []);
  return (
    <Layout>
      <table>
        <thead className="head">
          <td>sr. no.</td>
          <td>name</td>
          <td>email</td>
          <td>isDoctor</td>
          <td>isAdmin</td>
          <td>Actions</td>
        </thead>
        <tbody>
          {users?.data?.map((u) => (
            <tr>
              <td>{(count = count + 1)}</td>
              <td>{u.name}</td>
              <td>{u.email}</td>
              <td>{u.isDoctor ? "yes" : "no"}</td>
              <td>{u.isAdmin ? "yes" : "no"}</td>
              <td>{<button className="btn btn-danger">Block</button>}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </Layout>
  );
};

export default Users;
