import React, { useEffect, useState } from "react";
import Layout from "../../components/Layout";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate, useParams } from "react-router-dom";
import axios from "axios";
import { Col, Collapse, Form, Input, Row, TimePicker, message } from "antd";
import { hideLoading, showLoding } from "../../redux/features/alertSlice";

const Profile = () => {
  const { user } = useSelector((state) => state.user);
  const [doctor, setDoctor] = useState(null);
  const paramas = useParams();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const getDoctorInfo = async () => {
    try {
      const res = await axios.post(
        `${process.env.REACT_APP_API}/api/v1/doctor/getDoctorInfo`,
        { userId: paramas.id },
        {
          headers: {
            authorization: localStorage.getItem("token"),
          },
        }
      );
      if (res.data.success) {
        setDoctor(res?.data?.data);
      }
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    getDoctorInfo();
  }, []);
  const handleFinish = async (values) => {
    try {
      // message.success("hii");
      dispatch(showLoding(true));
      const res = await axios.post(
        `${process.env.REACT_APP_API}/api/v1/doctor/updateInfo`,
        { ...values, userId: user._id },
        {
          headers: {
            Authorization: `${localStorage.getItem("token")}`,
          },
        }
      );
      // console.log(res);
      dispatch(hideLoading(false));
      if (res?.data?.success) {
        message.success(res.data.message);
        navigate(`/doctor/profile/${user._id}`);
      } else {
        // console.log();
        message.error("error is here");
      }
    } catch (error) {
      console.log(error);
      dispatch(hideLoading(false));
    }
  };

  return (
    <Layout>
      <Form
        layout="vertical"
        className="form-d"
        onFinish={handleFinish}
        initialValue={doctor}
      >
        <h4 className="">personal details</h4>
        <Row gutter={20}>
          <Col xs={24} md={24} lg={8}>
            <Form.Item
              label="first name"
              name="firstName"
              required
              rules={[{ required: true }]}
            >
              <Input type="text" placeholder="firstName" />
            </Form.Item>
          </Col>{" "}
          <Col xs={24} md={24} lg={8}>
            <Form.Item
              label="last name"
              name="lastName"
              required
              rules={[{ required: true }]}
            >
              <Input type="text" placeholder="lastName" />
            </Form.Item>
          </Col>{" "}
          <Col xs={24} md={24} lg={8}>
            <Form.Item
              label="phone"
              name="phone"
              required
              rules={[{ required: true }]}
            >
              <Input type="text" placeholder="phone" />
            </Form.Item>
          </Col>{" "}
          <Col xs={24} md={24} lg={8}>
            <Form.Item
              label="email"
              name="email"
              required
              rules={[{ required: true }]}
            >
              <Input type="text" placeholder="email" />
            </Form.Item>
          </Col>{" "}
          <Col xs={24} md={24} lg={8}>
            <Form.Item
              label="website"
              name="website"
              required
              rules={[{ required: true }]}
            >
              <Input type="text" placeholder="website" />
            </Form.Item>
          </Col>{" "}
          <Col xs={24} md={24} lg={8}>
            <Form.Item
              label="address"
              name="address"
              required
              rules={[{ required: true }]}
            >
              <Input type="text" placeholder="address" />
            </Form.Item>
          </Col>
        </Row>
        <h2>professional details</h2>
        <Row gutter={20}>
          <Col xs={24} md={24} lg={8}>
            <Form.Item
              label="specialization"
              name="specialization"
              required
              rules={[{ required: true }]}
            >
              <Input type="text" placeholder="specialization" />
            </Form.Item>
          </Col>{" "}
          <Col xs={24} md={24} lg={8}>
            <Form.Item
              label="experince"
              name="experince"
              required
              rules={[{ required: true }]}
            >
              <Input type="text" placeholder="experince" />
            </Form.Item>
          </Col>{" "}
          <Col xs={24} md={24} lg={8}>
            <Form.Item
              label="feePerConsultation"
              name="feePerConsultation"
              required
              rules={[{ required: true }]}
            >
              <Input type="text" placeholder="feePerConsultation" />
            </Form.Item>
          </Col>{" "}
          <Col xs={24} md={24} lg={8}>
            <Form.Item
              label="timings"
              name="timings"
              required
              rules={[{ required: true }]}
            >
              <TimePicker.RangePicker></TimePicker.RangePicker>
            </Form.Item>
          </Col>
        </Row>
        <div className="d-flex justify-content-end">
          <button className="btn btn-primary" type="submit">
            submit
          </button>
        </div>
      </Form>
    </Layout>
  );
};

export default Profile;
