import React, { useDebugValue, useState } from "react";
import "../styles/RegisterStyle.css";
import axios from "axios";
import { Form, Input } from "antd";
import { Link, useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import { hideLoading, showLoding } from "../redux/features/alertSlice";
const Register = () => {
  const [user, setUser] = useState([]);
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const onFinishHandler = async (values) => {
    try {
      dispatch(showLoding());
      const { data } = await axios.post(`${process.env.REACT_APP_API}/api/v1/auth/register `, values);
      dispatch(hideLoading());
      if (data.success) {
        // setUser(data?.user)
        alert(data.message);
        navigate("/login");
      }
    } catch (error) {
      dispatch(hideLoading());
      console.log("something is wrong here");
    }
  };
  return (
    <>
      <div className="form-container">
        <Form
          layout="vertical"
          onFinish={onFinishHandler}
          className="p-3 regoister-form card"
        >
          <h1 className="text-center">Register form </h1>
          <Form.Item label="Name" name="name">
            <Input type="text" required />
          </Form.Item>
          <Form.Item label="Email" name="email">
            <Input type="text" required />
          </Form.Item>
          <Form.Item label="Password" name="password">
            <Input type="password" required />
          </Form.Item>
          <Link to={"/login"} className="btn btn-ingo">
            already user login
          </Link>
          <button className="btn btn-primary" type="submit">
            Register
          </button>
        </Form>
      </div>
    </>
  );
};

export default Register;
